:root{
    height: 100%;
}
html {
    font-size: 100%;
    height: 100%;
}

body {
    position: relative;
    font-style: normal;
    font-weight: normal;
    line-height: 1.3;
    display: flex;
    min-height: 100%;
    flex-direction: column;
    height: 100%;
    padding: 0;
    margin: 0;
}

*, *:before, *:after {
    box-sizing: border-box;
}

img {
    display: inline-block;
    max-width: 100%;
}

.main-screen, .category-screen, .quiz-screen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-title {
    font-family: "Roboto Light";
    font-size: 26px;
    max-width: 150px;
    text-align: center;
}


.category-btns {
    display: flex;
    gap: 10px;

    button {
        border: 1px solid rgb(47, 47, 47);
        border-radius: 30px;
        background: lightblue;
        padding: 10px;
    }
}